.d-flex {
	display: flex;
}
.d-none {
  display: none !important;
}
.flex-row-reverse {
	flex-direction: row-reverse;
}
.flex-column {
	flex-direction: column;
}
.justify-content-center {
	justify-content: center;
}
.justify-content-between {
	justify-content: space-between;
}
.align-items-center {
	align-items: center;
}
.align-items-end {
	align-items: flex-end;
}